
.journal-view {
  display: flex;
  flex-direction: column;
  height: 100%;
  background: linear-gradient(135deg, #1a1a2e 0%, #16213e 100%);
  color: #e0e0e0;
  border-radius: 12px;
  overflow: hidden;
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.4);
}

// ====================  ====================
.panel-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1.5rem 2rem;
  background: rgba(255, 255, 255, 0.05);
  border-bottom: 1px solid rgba(255, 255, 255, 0.1);

  .panel-title {
    font-size: 1.5rem;
    font-weight: 600;
    color: #f0f0f0;
    margin: 0;
  }

  .btn-close {
    background: none;
    border: none;
    color: #e0e0e0;
    font-size: 2rem;
    cursor: pointer;
    padding: 0;
    width: 32px;
    height: 32px;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 4px;
    transition: all 0.2s;

    &:hover {
      background: rgba(255, 255, 255, 0.1);
      color: #fff;
    }
  }
}

// ====================  ====================
.panel-content {
  display: flex;
  flex: 1;
  overflow: hidden;
}

// ====================  ====================
.journal-list {
  width: 300px;
  background: rgba(0, 0, 0, 0.2);
  border-right: 1px solid rgba(255, 255, 255, 0.1);
  display: flex;
  flex-direction: column;
  overflow: hidden;
}

.author-selector {
  padding: 1rem;
  background: rgba(255, 255, 255, 0.05);
  border-bottom: 1px solid rgba(255, 255, 255, 0.1);

  label {
    display: block;
    margin-bottom: 0.5rem;
    font-size: 0.9rem;
    color: #b0b0b0;
  }

  select {
    width: 100%;
    padding: 0.5rem;
    background: rgba(255, 255, 255, 0.1);
    border: 1px solid rgba(255, 255, 255, 0.2);
    border-radius: 4px;
    color: #e0e0e0;
    font-size: 0.9rem;
    cursor: pointer;

    &:focus {
      outline: none;
      border-color: #4a9eff;
    }

    option {
      background: #1a1a2e;
      color: #e0e0e0;
    }

    optgroup {
      background: #16213e;
      color: #b0b0b0;
      font-weight: 600;
    }
  }
}

.history-section {
  flex: 1;
  overflow-y: auto;
  padding: 1rem;

  .section-title {
    font-size: 1rem;
    font-weight: 600;
    color: #b0b0b0;
    margin: 0 0 1rem 0;
    padding-bottom: 0.5rem;
    border-bottom: 1px solid rgba(255, 255, 255, 0.1);
  }
}

.journal-items {
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
}

.journal-item {
  padding: 0.75rem;
  background: rgba(255, 255, 255, 0.05);
  border: 1px solid rgba(255, 255, 255, 0.1);
  border-radius: 6px;
  cursor: pointer;
  transition: all 0.2s;

  &:hover {
    background: rgba(255, 255, 255, 0.08);
    border-color: rgba(255, 255, 255, 0.2);
  }

  &.active {
    background: rgba(74, 158, 255, 0.2);
    border-color: #4a9eff;
  }

  .journal-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 0.5rem;

    .journal-date {
      font-size: 0.8rem;
      color: #b0b0b0;
    }

    .journal-edited {
      font-size: 0.75rem;
      color: #ffa500;
    }
  }

  .journal-preview {
    font-size: 0.85rem;
    color: #d0d0d0;
    line-height: 1.4;
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-line-clamp: 2;
    line-clamp: 2;
    -webkit-box-orient: vertical;
  }
}

.empty-state {
  text-align: center;
  padding: 2rem 1rem;
  color: #808080;
  font-size: 0.9rem;
}

// ====================  ====================
.journal-editor {
  flex: 1;
  display: flex;
  flex-direction: column;
  overflow: hidden;
}

.editor-content {
  display: flex;
  flex-direction: column;
  height: 100%;
}

.editor-header {
  padding: 1rem 1.5rem;
  background: rgba(255, 255, 255, 0.05);
  border-bottom: 1px solid rgba(255, 255, 255, 0.1);
  display: flex;
  justify-content: space-between;
  align-items: center;

  .editor-title {
    font-size: 1.1rem;
    font-weight: 600;
    color: #f0f0f0;
    margin: 0;
  }

  .editor-actions {
    display: flex;
    gap: 0.5rem;

    button {
      padding: 0.5rem 1rem;
      border: none;
      border-radius: 4px;
      font-size: 0.9rem;
      cursor: pointer;
      transition: all 0.2s;

      &:disabled {
        opacity: 0.5;
        cursor: not-allowed;
      }
    }

    .btn-generate {
      background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
      color: white;

      &:hover:not(:disabled) {
        transform: translateY(-2px);
        box-shadow: 0 4px 12px rgba(102, 126, 234, 0.4);
      }
    }

    .btn-save {
      background: linear-gradient(135deg, #4a9eff 0%, #2563eb 100%);
      color: white;

      &:hover:not(:disabled) {
        transform: translateY(-2px);
        box-shadow: 0 4px 12px rgba(74, 158, 255, 0.4);
      }
    }

    .btn-delete {
      background: linear-gradient(135deg, #ef4444 0%, #dc2626 100%);
      color: white;

      &:hover {
        transform: translateY(-2px);
        box-shadow: 0 4px 12px rgba(239, 68, 68, 0.4);
      }
    }

    .btn-new {
      background: rgba(255, 255, 255, 0.1);
      color: #e0e0e0;

      &:hover {
        background: rgba(255, 255, 255, 0.15);
      }
    }
  }
}

.editor-body {
  flex: 1;
  padding: 1.5rem;
  overflow: hidden;

  .journal-textarea {
    width: 100%;
    height: 100%;
    padding: 1rem;
    background: rgba(0, 0, 0, 0.3);
    border: 1px solid rgba(255, 255, 255, 0.1);
    border-radius: 6px;
    color: #e0e0e0;
    font-size: 1rem;
    line-height: 1.8;
    font-family: 'Microsoft YaHei', sans-serif;
    resize: none;

    &:focus {
      outline: none;
      border-color: #4a9eff;
      background: rgba(0, 0, 0, 0.4);
    }

    &:disabled {
      opacity: 0.6;
      cursor: not-allowed;
    }

    &::placeholder {
      color: #808080;
    }
  }
}

.editor-footer {
  padding: 1rem 1.5rem;
  background: rgba(255, 255, 255, 0.05);
  border-top: 1px solid rgba(255, 255, 255, 0.1);

  .editor-hint {
    margin: 0;
    font-size: 0.85rem;
    color: #b0b0b0;
    line-height: 1.5;
  }
}

.empty-editor {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
  color: #808080;
  font-size: 1.1rem;
}
