
@import '../styles/global.scss';

.character-creator {
  @include modal-container;
  width: 100%;
  max-width: 800px;
  max-height: 85vh;
  padding: $spacing-lg;
  overflow-y: auto;

  @include mobile {
    padding: $spacing-md;
    // 
    border-width: 1px;
  }

  @include small-screen {
    padding: $spacing-sm;
  }
}

.creator-header {
  text-align: center;
  margin-bottom: $spacing-lg;
  padding-bottom: $spacing-md;
  border-bottom: 1px solid $color-border-dark;

  @include mobile {
    margin-bottom: $spacing-md;
    padding-bottom: $spacing-sm;
  }
}

.creator-title {
  @include modal-title;
  margin-bottom: $spacing-xs;
}

.creator-subtitle {
  font-size: $font-size-sm;
  color: $color-text-secondary;

  @include mobile {
    font-size: $font-size-xs;
  }
}

.creator-content {
  display: flex;
  flex-direction: column;
  gap: $spacing-lg;
}

.section {
  background: rgba(0, 0, 0, 0.3);
  border: 1px solid $color-border-dark;
  border-radius: $border-radius-md;
  padding: $spacing-md;
}

.section-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: $spacing-md;
}

.section-title {
  font-size: $font-size-lg;
  font-weight: $font-weight-bold;
  color: $color-text-gold;
  margin-bottom: $spacing-md;

  @include mobile {
    font-size: $font-size-base;
    margin-bottom: $spacing-sm;
  }
}

.form-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  gap: $spacing-md;

  @include mobile {
    grid-template-columns: 1fr;
    gap: $spacing-sm;
  }
}

.form-group {
  display: flex;
  flex-direction: column;
  gap: $spacing-xs;
}

.form-label {
  font-size: $font-size-sm;
  font-weight: $font-weight-medium;
  color: $color-text-secondary;
}

.form-input,
.form-select,
.form-textarea {
  padding: $spacing-sm;
  background: rgba(0, 0, 0, 0.5);
  border: 1px solid $color-border-dark;
  border-radius: $border-radius-sm;
  color: $color-text-primary;
  font-size: $font-size-sm;
  font-family: $font-family-primary;
  transition: border-color $transition-fast;

  &:focus {
    outline: none;
    border-color: $color-primary-gold;
  }

  &::placeholder {
    color: $color-text-muted;
  }
}

.form-textarea {
  resize: vertical;
  min-height: 60px;
}

.attributes-grid {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: $spacing-md;

  @include mobile {
    grid-template-columns: repeat(2, 1fr);
    gap: $spacing-sm;
  }

  @include small-screen {
    grid-template-columns: 1fr;
  }
}

.attribute-item {
  display: flex;
  flex-direction: column;
  gap: $spacing-xs;
}

.attribute-label {
  font-size: $font-size-sm;
  font-weight: $font-weight-medium;
  color: $color-text-secondary;
}

.attribute-input {
  padding: $spacing-sm;
  background: rgba(0, 0, 0, 0.5);
  border: 1px solid $color-border-dark;
  border-radius: $border-radius-sm;
  color: $color-text-gold;
  font-size: $font-size-base;
  font-weight: $font-weight-bold;
  text-align: center;
  transition: border-color $transition-fast;

  &:focus {
    outline: none;
    border-color: $color-primary-gold;
  }
}

.derived-stats {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: $spacing-md;

  @include mobile {
    grid-template-columns: repeat(2, 1fr);
    gap: $spacing-sm;
  }

  @include small-screen {
    grid-template-columns: 1fr;
  }
}

.stat-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: $spacing-sm;
  background: rgba(0, 0, 0, 0.3);
  border-radius: $border-radius-sm;
}

.stat-label {
  font-size: $font-size-sm;
  color: $color-text-secondary;
}

.stat-value {
  font-size: $font-size-sm;
  font-weight: $font-weight-bold;
  color: $color-text-gold;
}

.skills-info {
  display: flex;
  gap: $spacing-lg;
  margin-bottom: $spacing-md;
  padding: $spacing-sm;
  background: rgba(212, 175, 55, 0.1);
  border-radius: $border-radius-sm;
}

.info-text {
  font-size: $font-size-sm;
  color: $color-text-secondary;
}

.points-value {
  font-weight: $font-weight-bold;
  color: $color-text-gold;

  &.points-negative {
    color: $color-danger;
  }
}

.skills-grid {
  display: flex;
  flex-direction: column;
  gap: $spacing-sm;
  margin-bottom: $spacing-md;
}

.skill-item {
  display: grid;
  grid-template-columns: 1fr auto auto auto;
  gap: $spacing-sm;
  align-items: center;
}

.skill-label {
  font-size: $font-size-sm;
  color: $color-text-secondary;
}

.skill-input {
  width: 80px;
  padding: $spacing-xs $spacing-sm;
  background: rgba(0, 0, 0, 0.5);
  border: 1px solid $color-border-dark;
  border-radius: $border-radius-sm;
  color: $color-text-gold;
  font-size: $font-size-sm;
  text-align: center;

  &:focus {
    outline: none;
    border-color: $color-primary-gold;
  }
}

.skill-type-select {
  width: 90px;
  padding: $spacing-xs $spacing-sm;
  background: rgba(0, 0, 0, 0.5);
  border: 1px solid $color-border-dark;
  border-radius: $border-radius-sm;
  color: $color-text-primary;
  font-size: $font-size-xs;
  cursor: pointer;

  &:focus {
    outline: none;
    border-color: $color-primary-gold;
  }
}

.add-skill {
  display: flex;
  gap: $spacing-sm;
}

.creator-actions {
  display: flex;
  justify-content: flex-end;
  gap: $spacing-md;
  margin-top: $spacing-lg;
  padding-top: $spacing-md;
  border-top: 1px solid $color-border-dark;

  @include mobile {
    flex-direction: column;
    gap: $spacing-sm;
  }
}

.btn-primary,
.btn-secondary,
.btn-cancel,
.btn-remove {
  padding: $spacing-sm $spacing-lg;
  border: none;
  border-radius: $border-radius-sm;
  font-size: $font-size-sm;
  font-weight: $font-weight-medium;
  cursor: pointer;
  transition: all $transition-fast;

  @include mobile {
    padding: $spacing-xs $spacing-md;
    font-size: $font-size-xs;
    min-height: 36px;
  }

  @include small-screen {
    padding: $spacing-xs $spacing-sm;
    min-height: 32px;
  }

  &:disabled {
    opacity: 0.5;
    cursor: not-allowed;
  }
}

.btn-primary {
  background: linear-gradient(135deg, $color-primary-gold, $color-secondary-gold);
  color: $color-primary-black;

  &:hover:not(:disabled) {
    transform: translateY(-2px);
    box-shadow: $shadow-gold;
  }
}

.btn-secondary {
  background: rgba(212, 175, 55, 0.2);
  color: $color-text-gold;
  border: 1px solid $color-border-gold;
  display: flex;
  align-items: center;
  gap: $spacing-xs;

  &:hover {
    background: rgba(212, 175, 55, 0.3);
  }
}

.btn-cancel {
  background: rgba(255, 255, 255, 0.1);
  color: $color-text-secondary;
  border: 1px solid $color-border-dark;

  &:hover {
    background: rgba(255, 255, 255, 0.15);
    color: $color-text-primary;
  }
}

.btn-remove {
  width: 28px;
  height: 28px;
  padding: 0;
  background: rgba(244, 67, 54, 0.2);
  color: $color-danger;
  border: 1px solid $color-danger;
  font-size: $font-size-lg;
  line-height: 1;

  &:hover {
    background: rgba(244, 67, 54, 0.3);
  }
}

.btn-icon {
  font-size: $font-size-base;
}
