
@import '../styles/global.scss';

.status-bar {
  position: fixed;
  top: calc($spacing-lg + 50px + $spacing-md); // 
  left: $spacing-lg;
  width: 220px;
  background: $color-bg-card;
  border: 2px solid $color-border-gold;
  border-radius: $border-radius-lg;
  padding: $spacing-sm $spacing-md;
  z-index: $z-index-ui;
  box-shadow: $shadow-md;

  // 
  @include mobile {
    top: calc($spacing-sm + 36px + $spacing-xs); // 
    left: $spacing-sm;
    width: 160px;
    padding: $spacing-xs $spacing-sm;
    border-width: 1px;
  }
}

.character-name {
  font-size: $font-size-base;
  font-weight: $font-weight-bold;
  color: $color-text-gold;
  text-align: center;
  margin-bottom: $spacing-sm;
  text-shadow: 0 0 10px rgba(212, 175, 55, 0.5);

  @include mobile {
    font-size: $font-size-sm;
    margin-bottom: $spacing-xs;
  }
}

.status-bars {
  display: flex;
  flex-direction: column;
  gap: $spacing-xs;
}

.stat-row {
  display: flex;
  align-items: center;
  gap: $spacing-sm;
}

.stat-label {
  font-size: $font-size-xs;
  font-weight: $font-weight-bold;
  color: $color-text-secondary;
  width: 32px;
  text-align: right;
}

.progress-bar {
  position: relative;
  flex: 1;
  height: 20px;
  background: rgba(0, 0, 0, 0.5);
  border: 1px solid $color-border-dark;
  border-radius: $border-radius-sm;
  overflow: hidden;

  @include mobile {
    height: 16px;
  }
}

.progress-fill {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  transition: width $transition-base;

  .hp-bar & {
    background: linear-gradient(90deg, darken($color-hp, 10%), $color-hp);
  }

  .mp-bar & {
    background: linear-gradient(90deg, darken($color-mp, 10%), $color-mp);
  }

  .san-bar & {
    background: linear-gradient(90deg, darken($color-san, 10%), $color-san);
  }
}

.stat-value {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  font-size: $font-size-xs;
  font-weight: $font-weight-bold;
  color: $color-text-primary;
  text-shadow: 0 1px 2px rgba(0, 0, 0, 0.8);
  z-index: 1;
}
