
@import '../styles/global.scss';

.api-config-panel {
  @include modal-container;
  max-width: 600px;
  width: 100%;
}

.panel-title {
  @include modal-title;
  text-align: center;
  margin-bottom: $spacing-sm;
}

.panel-description {
  font-size: $font-size-sm;
  color: $color-text-secondary;
  margin-bottom: $spacing-lg;
  text-align: center;
  line-height: 1.5;

  @include mobile {
    font-size: $font-size-xs;
    margin-bottom: $spacing-md;
  }
}

.current-api-info {
  background: rgba($color-primary-gold, 0.1);
  padding: $spacing-md;
  border-radius: $border-radius-sm;
  border: 1px solid $color-border-gold;
  margin-bottom: $spacing-lg;

  @include mobile {
    padding: $spacing-sm;
    margin-bottom: $spacing-md;
  }

  .info-row {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: $spacing-xs 0;

    &:not(:last-child) {
      border-bottom: 1px solid rgba($color-border-gold, 0.3);
    }

    @include mobile {
      flex-direction: column;
      align-items: flex-start;
      gap: $spacing-xs;
    }

    &.model-info {
      font-size: $font-size-sm;
      opacity: 0.9;

      @include mobile {
        font-size: $font-size-xs;
      }
    }
  }

  .info-label {
    color: $color-text-gold;
    font-size: $font-size-sm;

    @include mobile {
      font-size: $font-size-xs;
    }
  }

  .info-value {
    font-weight: $font-weight-bold;
    font-size: $font-size-base;

    @include mobile {
      font-size: $font-size-sm;
    }

    &.custom-api {
      color: $color-secondary-gold;
    }

    &.default-api {
      color: $color-info;
    }
  }
}

.config-tabs {
  display: flex;
  gap: $spacing-xs;
  margin-bottom: $spacing-lg;
  border-bottom: 2px solid $color-border-gold;

  @include mobile {
    margin-bottom: $spacing-md;
  }
}

.tab-button {
  flex: 1;
  padding: $spacing-sm $spacing-md;
  background: transparent;
  border: none;
  color: $color-text-secondary;
  cursor: pointer;
  transition: all $transition-base;
  border-bottom: 2px solid transparent;
  margin-bottom: -2px;
  font-size: $font-size-base;

  @include mobile {
    padding: $spacing-xs $spacing-sm;
    font-size: $font-size-sm;
  }

  &:hover {
    color: $color-text-primary;
    background: rgba($color-primary-gold, 0.1);
  }

  &.active {
    color: $color-secondary-gold;
    border-bottom-color: $color-secondary-gold;
    font-weight: $font-weight-bold;
  }
}

.config-form {
  margin-bottom: $spacing-xl;

  @include mobile {
    margin-bottom: $spacing-lg;
  }
}

.form-group {
  margin-bottom: $spacing-lg;

  @include mobile {
    margin-bottom: $spacing-md;
  }
}

.form-label {
  display: block;
  margin-bottom: $spacing-sm;
  color: $color-text-gold;
  font-size: $font-size-sm;

  @include mobile {
    font-size: $font-size-xs;
  }

  input[type='checkbox'] {
    margin-right: $spacing-sm;
    cursor: pointer;
  }
}

.form-input {
  @include form-control;
  width: 100%;

  &:hover {
    border-color: $color-secondary-gold;
  }

  &:focus {
    border-color: $color-secondary-gold;
    box-shadow: $shadow-gold;
  }
}

.model-input-group {
  .model-presets {
    display: flex;
    flex-wrap: wrap;
    gap: $spacing-xs;
    margin-top: $spacing-sm;

    @include mobile {
      gap: 4px;
    }
  }

  .preset-chip {
    padding: $spacing-xs $spacing-sm;
    background: rgba($color-primary-gold, 0.1);
    border: 1px solid $color-border-gold;
    border-radius: $border-radius-sm;
    color: $color-text-primary;
    font-size: $font-size-xs;
    cursor: pointer;
    transition: all $transition-base;

    @include mobile {
      padding: 4px $spacing-xs;
      font-size: 10px;
    }

    &:hover {
      background: rgba($color-secondary-gold, 0.2);
      border-color: $color-secondary-gold;
      transform: translateY(-1px);
    }

    &:active {
      transform: translateY(0);
    }
  }

  .model-hint {
    display: block;
    font-size: $font-size-xs;
    color: $color-text-secondary;
    margin-top: $spacing-xs;
  }
}

.api-key-hint {
  display: block;
  font-size: $font-size-xs;
  color: $color-text-secondary;
  margin-top: $spacing-xs;
  font-style: italic;
}

.form-range {
  width: 100%;
  margin-bottom: $spacing-xs;
  cursor: pointer;

  &::-webkit-slider-thumb {
    background: $color-primary-gold;
  }

  &::-moz-range-thumb {
    background: $color-primary-gold;
  }
}

.range-hint {
  display: block;
  font-size: $font-size-xs;
  color: $color-text-secondary;
  margin-top: $spacing-xs;
}

.form-actions {
  margin-top: $spacing-lg;

  @include mobile {
    margin-top: $spacing-md;
  }
}

.test-result {
  margin-top: $spacing-md;
  padding: $spacing-md;
  border-radius: $border-radius-sm;
  font-size: $font-size-sm;

  @include mobile {
    padding: $spacing-sm;
    font-size: $font-size-xs;
  }

  &.success {
    background: rgba($color-success, 0.2);
    border: 1px solid $color-success;
    color: lighten($color-success, 20%);
  }

  &.error {
    background: rgba($color-danger, 0.2);
    border: 1px solid $color-danger;
    color: lighten($color-danger, 20%);
  }
}

.panel-actions {
  @include button-group;
  justify-content: center;
}

.btn {
  padding: $spacing-sm $spacing-xl;
  border: none;
  border-radius: $border-radius-sm;
  cursor: pointer;
  font-size: $font-size-base;
  font-weight: $font-weight-bold;
  transition: all $transition-base;

  @include mobile {
    padding: $spacing-xs $spacing-lg;
    font-size: $font-size-sm;
  }

  &:disabled {
    opacity: 0.5;
    cursor: not-allowed;
  }

  &:hover:not(:disabled) {
    transform: translateY(-2px);
    box-shadow: $shadow-md;
  }

  &:active:not(:disabled) {
    transform: translateY(0);
  }
}

.btn-primary {
  background: linear-gradient(135deg, $color-primary-gold, $color-dark-gold);
  color: $color-primary-black;
  text-shadow: 0 1px 2px rgba(255, 255, 255, 0.3);

  &:hover:not(:disabled) {
    background: linear-gradient(135deg, $color-secondary-gold, $color-primary-gold);
    box-shadow: $shadow-md, $shadow-gold;
  }
}

.btn-secondary {
  background: rgba($color-text-secondary, 0.2);
  color: $color-text-primary;
  border: 1px solid $color-border-gold;

  &:hover:not(:disabled) {
    background: rgba($color-text-secondary, 0.3);
    border-color: $color-secondary-gold;
  }
}

.btn-test {
  background: linear-gradient(135deg, $color-info, darken($color-info, 10%));
  color: $color-text-primary;

  &:hover:not(:disabled) {
    background: linear-gradient(135deg, lighten($color-info, 10%), $color-info);
    box-shadow:
      $shadow-md,
      0 0 15px rgba(33, 150, 243, 0.5);
  }
}

.debug-mode-section {
  background: rgba($color-text-secondary, 0.1);
  padding: $spacing-md;
  border-radius: $border-radius-sm;
  border: 1px solid $color-border-gold;
  margin-bottom: $spacing-lg;

  @include mobile {
    padding: $spacing-sm;
    margin-bottom: $spacing-md;
  }

  .debug-mode-label {
    display: flex;
    align-items: center;
    color: $color-text-gold;
    font-size: $font-size-sm;
    cursor: pointer;

    @include mobile {
      font-size: $font-size-xs;
    }

    input[type='checkbox'] {
      margin-right: $spacing-sm;
      cursor: pointer;
      width: 16px;
      height: 16px;

      @include mobile {
        width: 14px;
        height: 14px;
      }
    }

    &:hover {
      color: $color-secondary-gold;
    }
  }
}
