
@import '../styles/global.scss';

.inventory-panel {
  @include modal-container;
  max-width: 900px;
  position: relative;
}

.close-btn {
  @include modal-close-button;
}

.panel-title {
  @include modal-title;
}

.inventory-tabs {
  display: flex;
  gap: $spacing-sm;
  justify-content: center;
  margin-bottom: $spacing-lg;

  @include mobile {
    gap: $spacing-xs;
    margin-bottom: $spacing-md;
  }
}

.tab-btn {
  padding: $spacing-sm $spacing-lg;
  background: rgba(0, 0, 0, 0.6);
  border: 2px solid $color-border-dark;
  border-radius: $border-radius-md;
  color: $color-text-secondary;
  font-size: $font-size-base;
  cursor: pointer;
  transition: all $transition-fast;

  @include mobile {
    padding: $spacing-xs $spacing-md;
    font-size: $font-size-sm;
    border-width: 1px;
  }

  &:hover {
    border-color: $color-border-gold;
    color: $color-text-gold;
  }

  &.active {
    background: rgba(212, 175, 55, 0.2);
    border-color: $color-border-gold;
    color: $color-text-gold;
    box-shadow: $shadow-gold;
  }
}

.inventory-content {
  min-height: 300px;

  @include mobile {
    min-height: 200px;
  }
}

.empty-inventory {
  text-align: center;
  padding: $spacing-2xl;
  color: $color-text-muted;

  @include mobile {
    padding: $spacing-xl;
  }

  p {
    font-size: $font-size-lg;
    margin: 0;

    @include mobile {
      font-size: $font-size-base;
    }
  }
}

.items-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
  gap: $spacing-md;

  @include mobile {
    grid-template-columns: 1fr;
    gap: $spacing-sm;
  }
}

.item-card {
  padding: $spacing-md;
  background: rgba(0, 0, 0, 0.6);
  border: 2px solid $color-border-dark;
  border-radius: $border-radius-md;
  cursor: pointer;
  transition: all $transition-fast;

  @include mobile {
    padding: $spacing-sm;
    border-width: 1px;
  }

  &:hover {
    border-color: $color-border-gold;
    transform: translateY(-2px);
    box-shadow: $shadow-gold;
  }
}

.item-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: $spacing-xs;
}

.item-name {
  font-size: $font-size-lg;
  font-weight: $font-weight-bold;
  color: $color-text-primary;

  @include mobile {
    font-size: $font-size-base;
  }
}

.item-type {
  font-size: $font-size-sm;
  color: $color-text-gold;
  padding: 2px 8px;
  background: rgba(212, 175, 55, 0.2);
  border-radius: $border-radius-sm;

  @include mobile {
    font-size: $font-size-xs;
    padding: 1px 6px;
  }
}

.item-description {
  color: $color-text-secondary;
  font-size: $font-size-sm;
  line-height: 1.5;
  margin: 0 0 $spacing-xs 0;

  @include mobile {
    font-size: $font-size-xs;
  }
}

.item-meta {
  margin-top: $spacing-xs;
}

.item-source {
  font-size: $font-size-xs;
  color: $color-text-muted;

  @include mobile {
    font-size: 10px;
  }
}

// 
.item-detail-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.8);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 10;
  backdrop-filter: blur(4px);
}

.item-detail-modal {
  background: $color-bg-card;
  border: 2px solid $color-border-gold;
  border-radius: $border-radius-lg;
  padding: $spacing-xl;
  max-width: 500px;
  width: 90%;
  box-shadow: $shadow-lg, $shadow-gold;

  @include mobile {
    padding: $spacing-lg;
    border-width: 1px;
  }

  h3 {
    font-size: $font-size-2xl;
    color: $color-text-gold;
    margin: 0 0 $spacing-md 0;

    @include mobile {
      font-size: $font-size-xl;
    }
  }
}

.detail-type {
  font-size: $font-size-base;
  color: $color-text-gold;
  margin: 0 0 $spacing-sm 0;

  @include mobile {
    font-size: $font-size-sm;
  }
}

.detail-description {
  font-size: $font-size-base;
  color: $color-text-secondary;
  line-height: 1.6;
  margin: 0 0 $spacing-md 0;

  @include mobile {
    font-size: $font-size-sm;
  }
}

.detail-meta {
  margin-bottom: $spacing-md;

  p {
    font-size: $font-size-sm;
    color: $color-text-muted;
    margin: $spacing-xs 0;

    @include mobile {
      font-size: $font-size-xs;
    }
  }
}

.detail-actions {
  display: flex;
  gap: $spacing-sm;
  justify-content: center;
  flex-wrap: wrap;
}

.btn-action {
  padding: $spacing-sm $spacing-lg;
  border: 2px solid;
  border-radius: $border-radius-md;
  font-size: $font-size-base;
  font-weight: $font-weight-bold;
  cursor: pointer;
  transition: all $transition-base;
  min-width: 80px;

  @include mobile {
    padding: $spacing-xs $spacing-md;
    font-size: $font-size-sm;
    border-width: 1px;
    min-width: 70px;
  }

  &.btn-use {
    background: linear-gradient(135deg, rgba(76, 175, 80, 0.3), rgba(76, 175, 80, 0.5));
    border-color: $color-success;
    color: $color-success;

    &:hover {
      background: linear-gradient(135deg, rgba(76, 175, 80, 0.5), rgba(76, 175, 80, 0.7));
      box-shadow: 0 0 20px rgba(76, 175, 80, 0.3);
      transform: translateY(-2px);
    }
  }

  &.btn-discard {
    background: linear-gradient(135deg, rgba(244, 67, 54, 0.3), rgba(244, 67, 54, 0.5));
    border-color: $color-danger;
    color: $color-danger;

    &:hover {
      background: linear-gradient(135deg, rgba(244, 67, 54, 0.5), rgba(244, 67, 54, 0.7));
      box-shadow: 0 0 20px rgba(244, 67, 54, 0.3);
      transform: translateY(-2px);
    }
  }

  &.btn-close-detail {
    background: linear-gradient(135deg, rgba(100, 100, 100, 0.3), rgba(100, 100, 100, 0.5));
    border-color: $color-text-muted;
    color: $color-text-secondary;

    &:hover {
      background: linear-gradient(135deg, rgba(100, 100, 100, 0.5), rgba(100, 100, 100, 0.7));
      box-shadow: 0 0 20px rgba(100, 100, 100, 0.3);
      transform: translateY(-2px);
    }
  }

  &:active {
    transform: translateY(0);
  }
}
