
@import '../styles/global.scss';

.character-panel {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 90%;
  max-width: 1200px;
  aspect-ratio: 16 / 9;
  background: $color-bg-card;
  border: 2px solid $color-border-gold;
  border-radius: $border-radius-lg;
  box-shadow: $shadow-lg;
  z-index: $z-index-panel;
  display: flex;
  flex-direction: column;
}

.panel-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: $spacing-md $spacing-lg;
  border-bottom: 1px solid $color-border-dark;
}

.panel-title {
  font-size: $font-size-xl;
  font-weight: $font-weight-bold;
  color: $color-text-gold;
  text-shadow: 0 0 10px rgba(212, 175, 55, 0.5);
}

.btn-close {
  width: 32px;
  height: 32px;
  padding: 0;
  background: rgba(255, 255, 255, 0.1);
  border: 1px solid $color-border-dark;
  border-radius: $border-radius-sm;
  color: $color-text-secondary;
  font-size: $font-size-2xl;
  line-height: 1;
  cursor: pointer;
  transition: all $transition-fast;

  &:hover {
    background: rgba(244, 67, 54, 0.2);
    border-color: $color-danger;
    color: $color-danger;
  }
}

.panel-content {
  flex: 1;
  padding: $spacing-lg;
  overflow: hidden;
}

.character-detail {
  height: 100%;
  overflow-y: auto;
  padding-right: $spacing-sm;
}

.detail-content {
  display: flex;
  flex-direction: column;
  gap: $spacing-lg;
}

.detail-section {
  background: rgba(0, 0, 0, 0.3);
  border: 1px solid $color-border-dark;
  border-radius: $border-radius-md;
  padding: $spacing-md;
}

.detail-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: $spacing-md;
  padding-bottom: $spacing-sm;
  border-bottom: 1px solid $color-border-dark;
}

.detail-title {
  font-size: $font-size-xl;
  font-weight: $font-weight-bold;
  color: $color-text-gold;
}

.detail-actions {
  display: flex;
  gap: $spacing-sm;
}

.btn-edit,
.btn-danger {
  padding: $spacing-xs $spacing-md;
  border: 1px solid;
  border-radius: $border-radius-sm;
  font-size: $font-size-sm;
  font-weight: $font-weight-medium;
  cursor: pointer;
  transition: all $transition-fast;
}

.btn-edit {
  background: rgba(33, 150, 243, 0.2);
  border-color: $color-info;
  color: $color-info;

  &:hover {
    background: rgba(33, 150, 243, 0.3);
  }
}

.btn-danger {
  background: rgba(244, 67, 54, 0.2);
  border-color: $color-danger;
  color: $color-danger;

  &:hover {
    background: rgba(244, 67, 54, 0.3);
  }
}

.basic-info {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(150px, 1fr));
  gap: $spacing-md;
  margin-bottom: $spacing-md;
}

.info-item {
  display: flex;
  gap: $spacing-xs;
  align-items: center;
}

.info-label {
  font-size: $font-size-sm;
  color: $color-text-secondary;
  font-weight: $font-weight-medium;
}

.info-value {
  font-size: $font-size-sm;
  color: $color-text-primary;
}

.affection-value {
  color: $color-text-gold;
  font-weight: $font-weight-bold;
}

.background-text {
  margin-top: $spacing-sm;
  padding-top: $spacing-sm;
  border-top: 1px solid $color-border-dark;
}

.background-content {
  margin-top: $spacing-xs;
  font-size: $font-size-sm;
  color: $color-text-secondary;
  line-height: 1.6;
}

.subsection-title {
  font-size: $font-size-base;
  font-weight: $font-weight-bold;
  color: $color-text-gold;
  margin-bottom: $spacing-md;
}

.attributes-grid {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: $spacing-md;
}

.attribute-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: $spacing-sm;
  background: rgba(0, 0, 0, 0.3);
  border-radius: $border-radius-sm;
}

.attribute-label {
  font-size: $font-size-sm;
  color: $color-text-secondary;
}

.attribute-value {
  font-size: $font-size-sm;
  font-weight: $font-weight-bold;
  color: $color-text-gold;
}

.derived-grid {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: $spacing-md;
}

.derived-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: $spacing-sm;
  background: rgba(0, 0, 0, 0.3);
  border-radius: $border-radius-sm;
}

.derived-label {
  font-size: $font-size-sm;
  color: $color-text-secondary;
}

.derived-value {
  font-size: $font-size-sm;
  font-weight: $font-weight-bold;
  color: $color-text-gold;
}

.body-parts {
  display: flex;
  flex-direction: column;
  gap: $spacing-sm;
}

.body-part-group {
  background: rgba(0, 0, 0, 0.3);
  border: 1px solid $color-border-dark;
  border-radius: $border-radius-sm;
  overflow: hidden;
}

.body-part-header {
  display: flex;
  align-items: center;
  gap: $spacing-sm;
  padding: $spacing-sm;
  cursor: pointer;
  transition: background $transition-fast;

  &:hover {
    background: rgba(255, 255, 255, 0.05);
  }
}

.expand-icon {
  font-size: $font-size-xs;
  color: $color-text-secondary;
  width: 12px;
}

.part-name {
  flex: 1;
  font-size: $font-size-sm;
  font-weight: $font-weight-medium;
  color: $color-text-primary;
}

.part-damage {
  font-size: $font-size-xs;
  font-weight: $font-weight-bold;
  padding: 2px $spacing-xs;
  border-radius: $border-radius-sm;
}

.damage-none {
  color: $color-success;
}

.damage-light {
  color: $color-info;
}

.damage-moderate {
  color: $color-warning;
}

.damage-heavy {
  color: #ff5722;
}

.damage-critical {
  color: $color-danger;
}

.body-part-children {
  padding: $spacing-xs $spacing-md $spacing-sm;
  background: rgba(0, 0, 0, 0.2);
  border-top: 1px solid $color-border-dark;
}

.body-part-child {
  display: flex;
  align-items: center;
  gap: $spacing-sm;
  padding: $spacing-xs;
  margin-bottom: $spacing-xs;

  &:last-child {
    margin-bottom: 0;
  }
}

.child-name {
  flex: 1;
  font-size: $font-size-xs;
  color: $color-text-secondary;
}

.child-damage {
  font-size: $font-size-xs;
  font-weight: $font-weight-bold;
}

.debuffs,
.part-debuffs {
  display: flex;
  flex-wrap: wrap;
  gap: $spacing-xs;
  padding: $spacing-xs $spacing-sm;
}

.debuff-tag {
  padding: 2px $spacing-xs;
  background: rgba(244, 67, 54, 0.2);
  border: 1px solid $color-danger;
  border-radius: $border-radius-sm;
  font-size: $font-size-xs;
  color: $color-danger;
}

.empty-detail {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
  color: $color-text-muted;
  font-size: $font-size-base;
}
