
@import './styles/global.scss';

// 
.loading-overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: $color-bg-primary;
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: $z-index-modal;
}

.loading-content {
  text-align: center;
  color: $color-text-gold;

  p {
    margin-top: $spacing-lg;
    font-size: $font-size-lg;
  }
}

.loading-spinner {
  width: 60px;
  height: 60px;
  border: 4px solid $color-border-dark;
  border-top-color: $color-primary-gold;
  border-radius: 50%;
  animation: spin 1s linear infinite;
  margin: 0 auto;
}

@keyframes spin {
  to {
    transform: rotate(360deg);
  }
}

// 
.error-overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: $color-bg-primary;
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: $z-index-modal;
}

.error-content {
  text-align: center;
  color: $color-text-primary;
  max-width: 500px;
  padding: $spacing-xl;

  h2 {
    color: $color-danger;
    margin-bottom: $spacing-lg;
    font-size: $font-size-2xl;
  }

  p {
    margin-bottom: $spacing-xl;
    font-size: $font-size-base;
    line-height: 1.6;
  }
}

.retry-button {
  padding: $spacing-md $spacing-xl;
  background: linear-gradient(135deg, $color-primary-gold, $color-dark-gold);
  border: none;
  border-radius: $border-radius-md;
  color: $color-primary-black;
  font-size: $font-size-base;
  font-weight: $font-weight-bold;
  cursor: pointer;
  transition: all $transition-base;

  &:hover {
    background: linear-gradient(135deg, $color-secondary-gold, $color-primary-gold);
    transform: translateY(-2px);
    box-shadow: $shadow-gold;
  }
}

.infinite-flow-game {
  position: relative;
  width: 100%;
  aspect-ratio: 16 / 9;
  overflow: hidden;
  background-color: $color-bg-primary;
}
