
@import '../styles/global.scss';

.sprite-layer {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: $z-index-sprite;
  pointer-events: none;
}

.character-sprite {
  position: absolute;
  bottom: 0;
  height: 80%;
  transition: transform $transition-base;

  &.position-left {
    left: 10%;
  }

  &.position-center {
    left: 50%;
    transform: translateX(-50%);
  }

  &.position-right {
    right: 10%;
  }

  &.position-custom {
    // 
    position: absolute;
  }
}

.sprite-image {
  height: 100%;
  width: auto;
  object-fit: contain;
  object-position: bottom;
}
