
@import '../styles/global.scss';

.main-interface {
  position: relative;
  width: 100%;
  aspect-ratio: 16 / 9; //  aspect-ratio  vh
  overflow: hidden;
}

// ====================  ====================
.layer {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  pointer-events: none; // 

  // 
  > * {
    pointer-events: auto;
  }
}

.layer-background {
  z-index: $z-index-background;
}

.layer-sprite {
  z-index: $z-index-sprite;
}

.layer-ui {
  z-index: $z-index-ui;
}

.control-panel {
  @include control-panel-position;

  // 
  &::after {
    content: '';
    position: sticky;
    bottom: 0;
    left: 0;
    right: 0;
    height: 20px;
    background: linear-gradient(to top, rgba(0, 0, 0, 0.5), transparent);
    pointer-events: none;
    opacity: 0;
    transition: opacity $transition-base;
  }

  // 
  &:hover::after {
    opacity: 1;
  }

  @include mobile {
    // 
    &::after {
      opacity: 0.7;
    }
  }
}

.control-btn {
  @include control-button;
  flex-shrink: 0; // 
}

.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.8);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: $z-index-modal;
  backdrop-filter: blur(4px);

  //  z-index
  &.character-creation-overlay {
    z-index: $z-index-character-creation;
    background: rgba(0, 0, 0, 0.9);
  }

  &.sanctuary-modal {
    .sanctuary-container {
      width: 90vw;
      max-width: 1400px;
      height: 80vh;
      max-height: 800px;
      background: $color-bg-card;
      border: 2px solid $color-border-gold;
      border-radius: $border-radius-lg;
      overflow: hidden;
      box-shadow: $shadow-lg, $shadow-gold;
    }
  }
}

.ui-toggle-btn {
  @include round-button;
  @include fixed-bottom-left;

  // UI 
  &.ui-hidden {
    opacity: 0.5;
    background: rgba(20, 20, 20, 0.6);

    &:hover {
      opacity: 0.8;
    }
  }
}

.toggle-dialogue-btn {
  @include round-button;
  @include fixed-bottom-right;

  &.active {
    background: rgba(212, 175, 55, 0.3);
    border-color: $color-secondary-gold;
    box-shadow: $shadow-gold;
  }
}

.fullscreen-btn {
  @include round-button;
  @include fixed-top-right;
}

.plot-review-btn {
  @include round-button;
  @include fixed-top-left;

  &.active {
    background: rgba(212, 175, 55, 0.3);
    border-color: $color-secondary-gold;
    box-shadow: $shadow-gold;
  }
}
