
@import '../styles/global.scss';

.instance-generator {
  width: 100%;
  aspect-ratio: 16 / 9;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: $spacing-xl;
  background: linear-gradient(135deg, $color-bg-primary 0%, $color-bg-secondary 100%);

  @include mobile {
    padding: $spacing-md;
    aspect-ratio: auto;
    min-height: 100vh;
  }

  @include small-screen {
    padding: $spacing-sm;
  }
}

.generator-container {
  @include modal-container;
  position: relative;
  max-width: 600px;
  width: 100%;
}

.close-btn {
  @include modal-close-button;
}

.generator-title {
  @include modal-title;
}

.generator-content {
  display: flex;
  flex-direction: column;
  gap: $spacing-lg;

  @include mobile {
    gap: $spacing-md;
  }
}

.form-group {
  display: flex;
  flex-direction: column;
  gap: $spacing-sm;

  label {
    font-size: $font-size-base;
    color: $color-text-gold;
    font-weight: $font-weight-medium;

    @include mobile {
      font-size: $font-size-sm;
    }
  }
}

.difficulty-selector {
  display: flex;
  gap: $spacing-sm;

  @include mobile {
    gap: 4px;
  }
}

.difficulty-btn {
  flex: 1;
  padding: $spacing-md;
  background: rgba(212, 175, 55, 0.1);
  border: 2px solid $color-border-gold;
  color: $color-text-gold;
  font-size: $font-size-lg;
  font-weight: $font-weight-bold;
  cursor: pointer;
  transition: all $transition-base;
  border-radius: $border-radius-sm;

  @include mobile {
    padding: $spacing-sm;
    font-size: $font-size-base;
    border-width: 1px;
  }

  @include small-screen {
    padding: $spacing-xs;
    font-size: $font-size-sm;
  }

  &:hover {
    background: rgba(212, 175, 55, 0.2);
    transform: translateY(-2px);
  }

  &.active {
    background: $color-primary-gold;
    color: $color-primary-black;
    box-shadow: $shadow-gold;
  }

  &:disabled {
    opacity: 0.5;
    cursor: not-allowed;
  }
}

.form-control {
  @include form-control;
}

textarea.form-control {
  resize: vertical;
  min-height: 100px;

  @include mobile {
    min-height: 80px;
  }
}

.button-group {
  @include button-group;
  margin-top: $spacing-md;
}

.btn {
  flex: 1;
  padding: $spacing-md;
  font-size: $font-size-lg;
  font-weight: $font-weight-bold;
  border: none;
  border-radius: $border-radius-sm;
  cursor: pointer;
  transition: all $transition-base;
  text-transform: uppercase;
  letter-spacing: 1px;

  &:disabled {
    opacity: 0.5;
    cursor: not-allowed;
  }
}

.btn-primary {
  background: linear-gradient(135deg, $color-primary-gold, $color-secondary-gold);
  color: $color-primary-black;
  box-shadow: $shadow-md, $shadow-gold;

  &:hover:not(:disabled) {
    transform: translateY(-2px);
    box-shadow: $shadow-lg, $shadow-gold;
  }

  &:active:not(:disabled) {
    transform: translateY(0);
  }
}

.btn-secondary {
  background: rgba(212, 175, 55, 0.2);
  color: $color-text-gold;
  border: 2px solid $color-border-gold;

  &:hover:not(:disabled) {
    background: rgba(212, 175, 55, 0.3);
    transform: translateY(-2px);
  }
}

.btn-success {
  background: linear-gradient(135deg, $color-success, lighten($color-success, 10%));
  color: $color-text-primary;
  box-shadow:
    $shadow-md,
    0 0 15px rgba(76, 175, 80, 0.4);

  &:hover {
    transform: translateY(-2px);
    box-shadow:
      $shadow-lg,
      0 0 20px rgba(76, 175, 80, 0.6);
  }
}

.generation-progress {
  display: flex;
  flex-direction: column;
  gap: $spacing-md;
  padding: $spacing-md;
  background: rgba(212, 175, 55, 0.1);
  border: 1px solid $color-border-gold;
  border-radius: $border-radius-sm;
}

.progress-bar {
  width: 100%;
  height: 8px;
  background: rgba(0, 0, 0, 0.5);
  border-radius: $border-radius-sm;
  overflow: hidden;
}

.progress-fill {
  height: 100%;
  background: linear-gradient(90deg, $color-primary-gold 0%, $color-secondary-gold 50%, $color-primary-gold 100%);
  background-size: 200% 100%;
  animation: progress-animation 2s linear infinite;
}

@keyframes progress-animation {
  0% {
    background-position: 200% 0;
  }
  100% {
    background-position: -200% 0;
  }
}

.progress-text {
  text-align: center;
  color: $color-text-gold;
  font-size: $font-size-base;
  margin: 0;
}

.generation-result {
  padding: $spacing-lg;
  background: rgba(76, 175, 80, 0.1);
  border: 2px solid $color-success;
  border-radius: $border-radius-sm;
}

.result-success {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: $spacing-md;

  .icon-check {
    width: 48px;
    height: 48px;
    border-radius: 50%;
    background: $color-success;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: $font-size-3xl;
    color: $color-text-primary;

    &::before {
      content: '';
    }
  }

  p {
    font-size: $font-size-lg;
    color: $color-success;
    margin: 0;
    font-weight: $font-weight-bold;
  }
}

// NPC 
.npc-management {
  .npc-list {
    display: flex;
    flex-direction: column;
    gap: $spacing-sm;
    margin-bottom: $spacing-md;
    max-height: 300px;
    overflow-y: auto;
    padding: $spacing-sm;
    background: rgba(0, 0, 0, 0.3);
    border-radius: $border-radius-sm;

    @include mobile {
      max-height: 200px;
    }
  }

  .npc-empty {
    text-align: center;
    padding: $spacing-lg;
    color: rgba($color-text-gold, 0.6);
    font-size: $font-size-sm;
  }

  .npc-item {
    display: flex;
    flex-direction: column;
    gap: $spacing-xs;
    padding: $spacing-sm;
    background: rgba(212, 175, 55, 0.1);
    border: 1px solid $color-border-gold;
    border-radius: $border-radius-sm;
    transition: all $transition-base;

    &:hover {
      background: rgba(212, 175, 55, 0.15);
    }

    @include mobile {
      padding: $spacing-xs;
    }
  }

  .npc-info {
    display: flex;
    gap: $spacing-md;
    align-items: center;

    @include mobile {
      flex-direction: column;
      gap: $spacing-xs;
      align-items: flex-start;
    }
  }

  .npc-name {
    font-size: $font-size-base;
    color: $color-text-gold;
    font-weight: $font-weight-bold;

    @include mobile {
      font-size: $font-size-sm;
    }
  }

  .npc-occupation {
    font-size: $font-size-sm;
    color: rgba($color-text-gold, 0.7);

    @include mobile {
      font-size: $font-size-xs;
    }
  }

  .npc-controls {
    display: flex;
    gap: $spacing-sm;
    align-items: center;

    @include mobile {
      flex-wrap: wrap;
    }
  }

  .importance-label {
    font-size: $font-size-sm;
    color: $color-text-gold;
    white-space: nowrap;

    @include mobile {
      font-size: $font-size-xs;
    }
  }

  .importance-selector {
    flex: 1;
    padding: $spacing-xs $spacing-sm;
    background: rgba(0, 0, 0, 0.5);
    border: 1px solid $color-border-gold;
    color: $color-text-gold;
    font-size: $font-size-sm;
    border-radius: $border-radius-sm;
    cursor: pointer;
    transition: all $transition-base;

    &:hover {
      background: rgba(0, 0, 0, 0.7);
    }

    &:focus {
      outline: none;
      border-color: $color-primary-gold;
    }

    @include mobile {
      font-size: $font-size-xs;
      padding: 4px $spacing-xs;
    }
  }

  .btn-delete {
    padding: $spacing-xs $spacing-sm;
    background: rgba(244, 67, 54, 0.2);
    border: 1px solid rgba(244, 67, 54, 0.5);
    color: #f44336;
    font-size: $font-size-base;
    font-weight: $font-weight-bold;
    border-radius: $border-radius-sm;
    cursor: pointer;
    transition: all $transition-base;

    &:hover {
      background: rgba(244, 67, 54, 0.3);
      transform: scale(1.1);
    }

    @include mobile {
      font-size: $font-size-sm;
      padding: 4px $spacing-xs;
    }
  }

  .npc-actions {
    display: flex;
    gap: $spacing-sm;

    @include mobile {
      flex-direction: column;
    }

    .btn {
      flex: 1;
      padding: $spacing-sm $spacing-md;
      font-size: $font-size-base;

      @include mobile {
        font-size: $font-size-sm;
        padding: $spacing-xs $spacing-sm;
      }
    }
  }
}

//  NPC 
.custom-npc-dialog {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.8);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1000;
  padding: $spacing-md;

  .dialog-content {
    background: linear-gradient(135deg, rgba(26, 26, 26, 0.98), rgba(38, 38, 38, 0.98));
    border: 2px solid $color-border-gold;
    border-radius: $border-radius-md;
    padding: $spacing-xl;
    max-width: 500px;
    width: 100%;
    box-shadow: $shadow-lg, $shadow-gold;

    @include mobile {
      padding: $spacing-md;
      max-width: 90%;
    }

    h3 {
      color: $color-text-gold;
      font-size: $font-size-xl;
      margin: 0 0 $spacing-md 0;
      text-align: center;

      @include mobile {
        font-size: $font-size-lg;
      }
    }

    .form-control {
      margin-bottom: $spacing-md;
    }

    .dialog-actions {
      display: flex;
      gap: $spacing-sm;

      @include mobile {
        flex-direction: column;
      }

      .btn {
        flex: 1;
        padding: $spacing-sm $spacing-md;
        font-size: $font-size-base;

        @include mobile {
          font-size: $font-size-sm;
        }
      }
    }
  }
}
