
@import '../styles/global.scss';

.plot-review {
  position: fixed;
  top: 0;
  left: 0;
  width: 400px;
  height: 100%;
  background: linear-gradient(135deg, rgba(20, 20, 30, 0.98), rgba(30, 20, 40, 0.98));
  border-right: 2px solid $color-border-gold;
  box-shadow:
    $shadow-lg,
    4px 0 20px rgba(212, 175, 55, 0.3);
  display: flex;
  flex-direction: column;
  z-index: $z-index-modal;
  animation: slideInLeft 0.3s ease-out;

  // 
  @include mobile {
    width: 85%;
    max-width: 320px;
    border-right-width: 1px;
  }
}

@keyframes slideInLeft {
  from {
    transform: translateX(-100%);
    opacity: 0;
  }
  to {
    transform: translateX(0);
    opacity: 1;
  }
}

.plot-review-header {
  padding: $spacing-lg;
  border-bottom: 1px solid rgba(212, 175, 55, 0.3);
  display: flex;
  align-items: center;
  justify-content: space-between;
  background: rgba(0, 0, 0, 0.3);

  @include mobile {
    padding: $spacing-md;
  }
}

.plot-review-controls {
  padding: $spacing-md;
  background: rgba(0, 0, 0, 0.2);
  border-bottom: 1px solid rgba(212, 175, 55, 0.2);
  display: flex;
  flex-direction: column;
  gap: $spacing-sm;

  @include mobile {
    padding: $spacing-sm;
  }
}

.control-group {
  display: flex;
  align-items: center;
  gap: $spacing-sm;

  &.search-group {
    flex: 1;
  }
}

.control-label {
  color: $color-text-gold;
  font-size: $font-size-sm;
  white-space: nowrap;
  min-width: 50px;

  @include mobile {
    font-size: $font-size-xs;
    min-width: 40px;
  }
}

.world-selector,
.instance-selector {
  flex: 1;
  padding: $spacing-xs $spacing-sm;
  background: rgba(20, 20, 20, 0.8);
  border: 1px solid $color-border-gold;
  border-radius: $border-radius-sm;
  color: $color-text-primary;
  font-size: $font-size-sm;
  cursor: pointer;
  transition: all $transition-fast;

  @include mobile {
    font-size: $font-size-xs;
    padding: $spacing-xs;
  }

  &:hover {
    border-color: $color-secondary-gold;
    background: rgba(212, 175, 55, 0.1);
  }

  &:focus {
    outline: none;
    border-color: $color-secondary-gold;
    box-shadow: 0 0 0 2px rgba(212, 175, 55, 0.2);
  }
}

.search-input {
  flex: 1;
  padding: $spacing-xs $spacing-sm;
  background: rgba(20, 20, 20, 0.8);
  border: 1px solid $color-border-gold;
  border-radius: $border-radius-sm;
  color: $color-text-primary;
  font-size: $font-size-sm;
  transition: all $transition-fast;

  @include mobile {
    font-size: $font-size-xs;
    padding: $spacing-xs;
  }

  &:focus {
    outline: none;
    border-color: $color-secondary-gold;
    box-shadow: 0 0 0 2px rgba(212, 175, 55, 0.2);
  }

  &::placeholder {
    color: $color-text-secondary;
  }
}

.search-btn {
  padding: $spacing-xs $spacing-sm;
  background: rgba(20, 20, 20, 0.8);
  border: 1px solid $color-border-gold;
  border-radius: $border-radius-sm;
  color: $color-text-gold;
  font-size: $font-size-base;
  cursor: pointer;
  transition: all $transition-fast;

  @include mobile {
    font-size: $font-size-sm;
    padding: $spacing-xs;
  }

  &:hover {
    background: rgba(212, 175, 55, 0.2);
    border-color: $color-secondary-gold;
    transform: scale(1.05);
  }

  &:active {
    transform: scale(0.95);
  }
}

.ai-review-btn {
  width: 100%;
  padding: $spacing-sm;
  background: linear-gradient(135deg, rgba(138, 43, 226, 0.3), rgba(75, 0, 130, 0.3));
  border: 1px solid rgba(138, 43, 226, 0.5);
  border-radius: $border-radius-sm;
  color: $color-text-gold;
  font-size: $font-size-sm;
  font-weight: bold;
  cursor: pointer;
  transition: all $transition-fast;

  @include mobile {
    font-size: $font-size-xs;
    padding: $spacing-xs;
  }

  &:hover:not(:disabled) {
    background: linear-gradient(135deg, rgba(138, 43, 226, 0.5), rgba(75, 0, 130, 0.5));
    border-color: rgba(138, 43, 226, 0.8);
    transform: translateY(-2px);
    box-shadow: 0 4px 8px rgba(138, 43, 226, 0.3);
  }

  &:active:not(:disabled) {
    transform: translateY(0);
  }

  &:disabled {
    opacity: 0.5;
    cursor: not-allowed;
  }
}

.ai-review-section {
  margin-bottom: $spacing-md;
  padding: $spacing-md;
  background: linear-gradient(135deg, rgba(138, 43, 226, 0.1), rgba(75, 0, 130, 0.1));
  border: 1px solid rgba(138, 43, 226, 0.3);
  border-radius: $border-radius-md;

  @include mobile {
    padding: $spacing-sm;
    margin-bottom: $spacing-sm;
  }
}

.ai-review-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: $spacing-sm;
  padding-bottom: $spacing-sm;
  border-bottom: 1px solid rgba(138, 43, 226, 0.3);

  h3 {
    margin: 0;
    color: $color-text-gold;
    font-size: $font-size-lg;
    text-shadow: $text-shadow-gold;

    @include mobile {
      font-size: $font-size-base;
    }
  }
}

.close-review-btn {
  width: 24px;
  height: 24px;
  padding: 0;
  background: rgba(139, 0, 0, 0.3);
  border: 1px solid rgba(220, 20, 60, 0.5);
  border-radius: $border-radius-sm;
  color: $color-danger;
  font-size: $font-size-base;
  cursor: pointer;
  transition: all $transition-fast;

  @include mobile {
    width: 20px;
    height: 20px;
    font-size: $font-size-sm;
  }

  &:hover {
    background: rgba(220, 20, 60, 0.3);
    border-color: $color-danger;
    transform: scale(1.1);
  }
}

.ai-review-content {
  color: $color-text-primary;
  font-size: $font-size-base;
  line-height: 1.8;
  white-space: pre-wrap;
  word-wrap: break-word;

  @include mobile {
    font-size: $font-size-sm;
    line-height: 1.6;
  }
}

.plot-review-title {
  margin: 0;
  font-size: $font-size-xl;
  color: $color-text-gold;
  text-shadow: $text-shadow-gold;
  font-family: $font-family-title;

  @include mobile {
    font-size: $font-size-lg;
  }
}

.close-btn {
  width: 32px;
  height: 32px;
  padding: 0;
  background: rgba(139, 0, 0, 0.3);
  border: 1px solid rgba(220, 20, 60, 0.5);
  border-radius: $border-radius-sm;
  color: $color-danger;
  font-size: $font-size-lg;
  cursor: pointer;
  transition: all $transition-fast;
  flex-shrink: 0;

  @include mobile {
    width: 28px;
    height: 28px;
    font-size: $font-size-base;
  }

  &:hover {
    background: rgba(220, 20, 60, 0.3);
    border-color: $color-danger;
    transform: scale(1.1);
  }
}

.plot-review-content {
  flex: 1;
  overflow-y: auto;
  padding: $spacing-md;

  @include mobile {
    padding: $spacing-sm;
  }

  &::-webkit-scrollbar {
    width: 8px;

    @include mobile {
      width: 4px;
    }
  }

  &::-webkit-scrollbar-track {
    background: rgba(0, 0, 0, 0.3);
  }

  &::-webkit-scrollbar-thumb {
    background: rgba(212, 175, 55, 0.3);
    border-radius: 4px;

    &:hover {
      background: rgba(212, 175, 55, 0.5);
    }
  }
}

.loading-state,
.empty-state {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100%;
  color: $color-text-secondary;
  font-size: $font-size-lg;
  gap: $spacing-md;

  p:first-child {
    font-size: 48px;
    margin: 0;
  }

  p:last-child {
    margin: 0;
  }
}

.loading-spinner {
  width: 40px;
  height: 40px;
  border: 3px solid rgba(212, 175, 55, 0.2);
  border-top-color: $color-border-gold;
  border-radius: 50%;
  animation: spin 1s linear infinite;
}

@keyframes spin {
  to {
    transform: rotate(360deg);
  }
}

.messages-list {
  display: flex;
  flex-direction: column;
  gap: $spacing-md;

  @include mobile {
    gap: $spacing-sm;
  }
}

.message-item {
  background: rgba(0, 0, 0, 0.4);
  border: 1px solid rgba(212, 175, 55, 0.2);
  border-radius: $border-radius-md;
  padding: $spacing-md;
  transition: all $transition-fast;

  @include mobile {
    padding: $spacing-sm;
  }

  &:hover {
    border-color: rgba(212, 175, 55, 0.4);
    background: rgba(0, 0, 0, 0.5);
    box-shadow: 0 2px 8px rgba(212, 175, 55, 0.2);
  }

  &.is-user {
    border-left: 3px solid $color-info;
  }

  &.is-system {
    border-left: 3px solid $color-warning;
    background: rgba(255, 193, 7, 0.05);
  }

  &.is-highlighted {
    border-color: rgba(138, 43, 226, 0.5);
    background: rgba(138, 43, 226, 0.1);
    box-shadow: 0 0 10px rgba(138, 43, 226, 0.3);
  }
}

.message-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: $spacing-sm;
  padding-bottom: $spacing-xs;
  border-bottom: 1px solid rgba(212, 175, 55, 0.2);
}

.message-name {
  font-weight: bold;
  color: $color-text-gold;
  font-size: $font-size-base;

  @include mobile {
    font-size: $font-size-sm;
  }
}

.message-index {
  font-size: $font-size-sm;
  color: $color-text-secondary;
  font-family: monospace;

  @include mobile {
    font-size: $font-size-xs;
  }
}

.message-content {
  color: $color-text-primary;
  font-size: $font-size-base;
  line-height: 1.6;
  word-wrap: break-word;
  white-space: pre-wrap;
  cursor: pointer;
  transition: all $transition-fast;
  position: relative;

  @include mobile {
    font-size: $font-size-sm;
    line-height: 1.5;
  }

  &.is-collapsed {
    display: -webkit-box;
    -webkit-line-clamp: 3;
    line-clamp: 3;
    -webkit-box-orient: vertical;
    overflow: hidden;
    text-overflow: ellipsis;

    &::after {
      content: '...';
      position: absolute;
      bottom: 0;
      right: 0;
      padding-left: 20px;
      background: linear-gradient(to right, transparent, rgba(0, 0, 0, 0.4) 20%);
    }
  }

  &:hover {
    color: $color-text-gold;
  }
}

.plot-review-footer {
  padding: $spacing-md;
  border-top: 1px solid rgba(212, 175, 55, 0.3);
  display: flex;
  gap: $spacing-sm;
  background: rgba(0, 0, 0, 0.3);

  @include mobile {
    padding: $spacing-sm;
    gap: $spacing-xs;
  }
}

.action-btn {
  flex: 1;
  padding: $spacing-sm $spacing-md;
  background: rgba(20, 20, 20, 0.8);
  border: 1px solid $color-border-gold;
  border-radius: $border-radius-sm;
  color: $color-text-gold;
  font-size: $font-size-sm;
  cursor: pointer;
  transition: all $transition-fast;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 4px;

  @include mobile {
    padding: $spacing-xs $spacing-sm;
    font-size: $font-size-xs;
    gap: 2px;
  }

  span:first-child {
    font-size: $font-size-lg;

    @include mobile {
      font-size: $font-size-base;
    }
  }

  &:hover {
    background: rgba(212, 175, 55, 0.2);
    border-color: $color-secondary-gold;
    transform: translateY(-2px);
    box-shadow: 0 4px 8px rgba(212, 175, 55, 0.3);
  }

  &:active {
    transform: translateY(0);
  }
}
