
@import '../styles/global.scss';

.dice-roller {
  @include modal-container;
  max-width: 400px;
  width: 100%;
  position: relative;

  &.is-rolling {
    pointer-events: none;
  }
}

.roller-header {
  text-align: center;
  margin-bottom: $spacing-lg;

  @include mobile {
    margin-bottom: $spacing-md;
  }

  h3 {
    @include modal-title;
    margin: 0;
    margin-bottom: 0;
  }
}

.skill-selection {
  margin-bottom: $spacing-lg;

  @include mobile {
    margin-bottom: $spacing-md;
  }

  label {
    display: block;
    color: $color-text-gold;
    font-size: $font-size-sm;
    margin-bottom: $spacing-sm;

    @include mobile {
      font-size: $font-size-xs;
    }
  }

  select {
    @include form-control;
    width: 100%;
    cursor: pointer;

    &:hover:not(:disabled) {
      border-color: $color-secondary-gold;
      box-shadow: $shadow-gold;
    }

    &:disabled {
      opacity: 0.5;
      cursor: not-allowed;
    }

    option {
      background: $color-primary-black;
      color: $color-text-primary;
    }
  }
}

.roll-actions {
  @include button-group;
  margin-bottom: $spacing-lg;

  button {
    flex: 1;
    padding: $spacing-sm $spacing-lg;
    background: linear-gradient(135deg, $color-primary-gold, $color-dark-gold);
    border: none;
    border-radius: $border-radius-sm;
    color: $color-primary-black;
    font-size: $font-size-base;
    font-weight: $font-weight-bold;
    cursor: pointer;
    transition: all $transition-base;
    text-shadow: 0 1px 2px rgba(255, 255, 255, 0.3);

    @include mobile {
      padding: $spacing-xs $spacing-md;
      font-size: $font-size-sm;
    }

    &:hover:not(:disabled) {
      background: linear-gradient(135deg, $color-secondary-gold, $color-primary-gold);
      box-shadow: $shadow-md, $shadow-gold;
      transform: translateY(-2px);
    }

    &:active:not(:disabled) {
      transform: translateY(0);
    }

    &:disabled {
      opacity: 0.5;
      cursor: not-allowed;
    }
  }

  .btn-reroll {
    background: linear-gradient(135deg, #888888, #666666);

    &:hover:not(:disabled) {
      background: linear-gradient(135deg, #aaaaaa, #888888);
    }
  }
}

.roll-result {
  padding: $spacing-lg;
  background: rgba(212, 175, 55, 0.1);
  border: 1px solid $color-border-gold;
  border-radius: $border-radius-sm;

  @include mobile {
    padding: $spacing-md;
  }
}

.dice-display {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: $spacing-lg;

  @include mobile {
    margin-bottom: $spacing-md;
  }

  .dice-value {
    width: 80px;
    height: 80px;
    display: flex;
    justify-content: center;
    align-items: center;
    background: linear-gradient(135deg, $color-primary-gold, $color-dark-gold);
    border: 3px solid $color-secondary-gold;
    border-radius: $border-radius-md;
    color: $color-primary-black;
    font-size: $font-size-3xl;
    font-weight: $font-weight-bold;
    box-shadow: $shadow-md, $shadow-gold;
    text-shadow: 0 2px 4px rgba(255, 255, 255, 0.3);

    @include mobile {
      width: 60px;
      height: 60px;
      font-size: $font-size-2xl;
      border-width: 2px;
    }
  }
}

.result-info {
  text-align: center;
  margin-bottom: $spacing-md;

  @include mobile {
    margin-bottom: $spacing-sm;
  }
}

.result-level {
  font-size: $font-size-2xl;
  font-weight: $font-weight-bold;
  margin-bottom: $spacing-sm;
  text-shadow: 0 0 10px currentColor;

  @include mobile {
    font-size: $font-size-xl;
  }

  &.result-critical_success {
    color: $color-success;
  }

  &.result-extreme_success {
    color: darken($color-success, 10%);
  }

  &.result-hard_success {
    color: lighten($color-success, 20%);
  }

  &.result-success {
    color: lighten($color-success, 30%);
  }

  &.result-failure {
    color: lighten($color-danger, 30%);
  }

  &.result-critical_failure {
    color: $color-danger;
  }
}

.result-details {
  display: flex;
  justify-content: space-around;
  color: $color-text-gold;
  font-size: $font-size-sm;
  gap: $spacing-xs;
  flex-wrap: wrap;

  @include mobile {
    font-size: $font-size-xs;
  }

  span {
    padding: $spacing-xs $spacing-sm;
    background: rgba(0, 0, 0, 0.5);
    border-radius: $border-radius-sm;

    @include mobile {
      padding: 4px $spacing-xs;
    }
  }
}

.btn-generate {
  width: 100%;
  padding: $spacing-sm $spacing-lg;
  background: linear-gradient(135deg, $color-info, darken($color-info, 10%));
  border: none;
  border-radius: $border-radius-sm;
  color: $color-text-primary;
  font-size: $font-size-base;
  font-weight: $font-weight-bold;
  cursor: pointer;
  transition: all $transition-base;

  @include mobile {
    padding: $spacing-xs $spacing-md;
    font-size: $font-size-sm;
  }

  &:hover:not(:disabled) {
    background: linear-gradient(135deg, lighten($color-info, 10%), $color-info);
    box-shadow:
      $shadow-md,
      0 0 15px rgba(33, 150, 243, 0.5);
    transform: translateY(-2px);
  }

  &:active:not(:disabled) {
    transform: translateY(0);
  }

  &:disabled {
    opacity: 0.5;
    cursor: not-allowed;
  }
}

// 
.fade-enter-active,
.fade-leave-active {
  transition: opacity $transition-base;
}

.fade-enter-from,
.fade-leave-to {
  opacity: 0;
}
