
@import '../styles/global.scss';

.area-details-modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.9);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: $z-index-modal + 10;
  backdrop-filter: blur(4px);
  padding: $spacing-md;

  @include mobile {
    padding: $spacing-sm;
  }
}

.area-details-modal {
  @include modal-container;
  max-width: 700px;
  position: relative;
}

.close-btn {
  @include modal-close-button;
}

.modal-title {
  @include modal-title;
}

.loading {
  text-align: center;
  padding: $spacing-2xl;
  color: $color-text-secondary;

  @include mobile {
    padding: $spacing-xl;
  }
}

.details-content {
  display: flex;
  flex-direction: column;
  gap: $spacing-lg;

  @include mobile {
    gap: $spacing-md;
  }
}

.detail-section {
  h3 {
    font-size: $font-size-lg;
    color: $color-text-gold;
    margin-bottom: $spacing-sm;

    @include mobile {
      font-size: $font-size-base;
    }
  }

  p {
    color: $color-text-secondary;
    font-size: $font-size-base;
    line-height: 1.6;

    @include mobile {
      font-size: $font-size-sm;
    }
  }
}

.items-list {
  display: flex;
  flex-direction: column;
  gap: $spacing-sm;
}

.item-card {
  padding: $spacing-md;
  background: rgba(0, 0, 0, 0.6);
  border: 2px solid $color-border-dark;
  border-radius: $border-radius-md;
  cursor: pointer;
  transition: all $transition-fast;

  @include mobile {
    padding: $spacing-sm;
    border-width: 1px;
  }

  &:hover {
    border-color: $color-border-gold;
    transform: translateY(-2px);
    box-shadow: $shadow-gold;
  }

  &.selected {
    border-color: $color-secondary-gold;
    background: rgba(212, 175, 55, 0.2);
    box-shadow: $shadow-gold;
  }
}

.item-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: $spacing-xs;
}

.item-name {
  font-size: $font-size-lg;
  font-weight: $font-weight-bold;
  color: $color-text-primary;

  @include mobile {
    font-size: $font-size-base;
  }
}

.item-type {
  font-size: $font-size-sm;
  color: $color-text-gold;
  padding: 2px 8px;
  background: rgba(212, 175, 55, 0.2);
  border-radius: $border-radius-sm;

  @include mobile {
    font-size: $font-size-xs;
    padding: 1px 6px;
  }
}

.item-description {
  color: $color-text-secondary;
  font-size: $font-size-sm;
  line-height: 1.5;
  margin: 0;

  @include mobile {
    font-size: $font-size-xs;
  }
}

.item-actions {
  display: flex;
  gap: $spacing-md;
  margin-top: $spacing-md;
  justify-content: center;

  @include mobile {
    gap: $spacing-sm;
  }
}

.btn-action {
  padding: $spacing-sm $spacing-lg;
  border: 2px solid;
  border-radius: $border-radius-md;
  font-size: $font-size-base;
  font-weight: $font-weight-bold;
  cursor: pointer;
  transition: all $transition-base;
  min-width: 100px;

  @include mobile {
    padding: $spacing-xs $spacing-md;
    font-size: $font-size-sm;
    border-width: 1px;
    min-width: 80px;
  }

  &.btn-pickup {
    background: linear-gradient(135deg, rgba(76, 175, 80, 0.3), rgba(76, 175, 80, 0.5));
    border-color: $color-success;
    color: $color-success;

    &:hover {
      background: linear-gradient(135deg, rgba(76, 175, 80, 0.5), rgba(76, 175, 80, 0.7));
      box-shadow: 0 0 20px rgba(76, 175, 80, 0.3);
      transform: translateY(-2px);
    }

    &:active {
      transform: translateY(0);
    }
  }

  &.btn-cancel-item {
    background: linear-gradient(135deg, rgba(244, 67, 54, 0.3), rgba(244, 67, 54, 0.5));
    border-color: $color-danger;
    color: $color-danger;

    &:hover {
      background: linear-gradient(135deg, rgba(244, 67, 54, 0.5), rgba(244, 67, 54, 0.7));
      box-shadow: 0 0 20px rgba(244, 67, 54, 0.3);
      transform: translateY(-2px);
    }

    &:active {
      transform: translateY(0);
    }
  }
}

.no-items {
  text-align: center;
  padding: $spacing-xl;
  color: $color-text-muted;

  @include mobile {
    padding: $spacing-lg;
  }

  p {
    margin: 0;
    font-size: $font-size-base;

    @include mobile {
      font-size: $font-size-sm;
    }
  }
}
