
@import '../styles/global.scss';

.map-panel {
  @include modal-container;
  width: 100%;
  max-width: 800px;
}

.map-header {
  text-align: center;
  margin-bottom: $spacing-lg;

  @include mobile {
    margin-bottom: $spacing-md;
  }

  h2 {
    @include modal-title;
    margin: 0;
    margin-bottom: $spacing-md;
  }
}

.map-tabs {
  display: flex;
  gap: $spacing-sm;
  justify-content: center;
  margin-bottom: $spacing-md;

  @include mobile {
    gap: $spacing-xs;
    margin-bottom: $spacing-sm;
  }
}

.tab-btn {
  padding: $spacing-sm $spacing-lg;
  background: rgba(0, 0, 0, 0.6);
  border: 2px solid $color-border-dark;
  border-radius: $border-radius-md;
  color: $color-text-secondary;
  font-size: $font-size-base;
  cursor: pointer;
  transition: all $transition-fast;

  @include mobile {
    padding: $spacing-xs $spacing-md;
    font-size: $font-size-sm;
    border-width: 1px;
  }

  &:hover {
    border-color: $color-border-gold;
    color: $color-text-gold;
  }

  &.active {
    background: rgba(212, 175, 55, 0.2);
    border-color: $color-border-gold;
    color: $color-text-gold;
    box-shadow: $shadow-gold;
  }
}

.instance-name {
  font-size: $font-size-base;
  color: $color-text-secondary;
  margin-top: $spacing-sm;

  @include mobile {
    font-size: $font-size-sm;
  }
}

.no-map {
  text-align: center;
  padding: $spacing-2xl;
  color: $color-text-secondary;

  @include mobile {
    padding: $spacing-xl;
  }

  p {
    margin-bottom: $spacing-lg;
    font-size: $font-size-lg;

    @include mobile {
      font-size: $font-size-base;
      margin-bottom: $spacing-md;
    }
  }
}

.btn-generate {
  padding: $spacing-md $spacing-xl;
  background: linear-gradient(135deg, rgba(212, 175, 55, 0.3), rgba(212, 175, 55, 0.5));
  border: 2px solid $color-border-gold;
  border-radius: $border-radius-md;
  color: $color-text-gold;
  font-size: $font-size-base;
  font-weight: $font-weight-bold;
  cursor: pointer;
  transition: all $transition-base;

  @include mobile {
    padding: $spacing-sm $spacing-lg;
    font-size: $font-size-sm;
    border-width: 1px;
  }

  &:hover {
    background: linear-gradient(135deg, rgba(212, 175, 55, 0.5), rgba(212, 175, 55, 0.7));
    box-shadow: $shadow-gold;
    transform: translateY(-2px);
  }

  &:active {
    transform: translateY(0);
  }
}

.current-area,
.connected-areas {
  margin-bottom: $spacing-lg;

  @include mobile {
    margin-bottom: $spacing-md;
  }

  h3 {
    font-size: $font-size-lg;
    color: $color-text-gold;
    margin-bottom: $spacing-md;

    @include mobile {
      font-size: $font-size-base;
      margin-bottom: $spacing-sm;
    }
  }
}

.areas-list {
  display: flex;
  flex-direction: column;
  gap: $spacing-sm;
}

.area-card {
  padding: $spacing-md;
  background: rgba(0, 0, 0, 0.6);
  border: 2px solid $color-border-dark;
  border-radius: $border-radius-md;
  cursor: pointer;
  transition: all $transition-fast;

  @include mobile {
    padding: $spacing-sm;
    border-width: 1px;
  }

  &:hover {
    border-color: $color-border-gold;
    transform: translateY(-2px);
    box-shadow: $shadow-gold;
  }

  &.current {
    border-color: $color-primary-gold;
    cursor: default;
    background: rgba(212, 175, 55, 0.1);

    &:hover {
      transform: none;
    }
  }

  &.discovered {
    border-color: $color-border-gold;
  }

  &.selected {
    border-color: $color-secondary-gold;
    background: rgba(212, 175, 55, 0.2);
    box-shadow: $shadow-gold;
  }
}

.action-buttons {
  display: flex;
  gap: $spacing-md;
  margin-top: $spacing-lg;
  justify-content: center;

  @include mobile {
    gap: $spacing-sm;
    margin-top: $spacing-md;
  }
}

.btn-action {
  padding: $spacing-md $spacing-xl;
  border: 2px solid;
  border-radius: $border-radius-md;
  font-size: $font-size-base;
  font-weight: $font-weight-bold;
  cursor: pointer;
  transition: all $transition-base;
  min-width: 100px;

  @include mobile {
    padding: $spacing-sm $spacing-lg;
    font-size: $font-size-sm;
    border-width: 1px;
    min-width: 80px;
  }

  &.btn-move {
    background: linear-gradient(135deg, rgba(33, 150, 243, 0.3), rgba(33, 150, 243, 0.5));
    border-color: $color-info;
    color: $color-info;

    &:hover {
      background: linear-gradient(135deg, rgba(33, 150, 243, 0.5), rgba(33, 150, 243, 0.7));
      box-shadow: 0 0 20px rgba(33, 150, 243, 0.3);
      transform: translateY(-2px);
    }

    &:active {
      transform: translateY(0);
    }
  }

  &.btn-confirm {
    background: linear-gradient(135deg, rgba(76, 175, 80, 0.3), rgba(76, 175, 80, 0.5));
    border-color: $color-success;
    color: $color-success;

    &:hover {
      background: linear-gradient(135deg, rgba(76, 175, 80, 0.5), rgba(76, 175, 80, 0.7));
      box-shadow: 0 0 20px rgba(76, 175, 80, 0.3);
      transform: translateY(-2px);
    }

    &:active {
      transform: translateY(0);
    }
  }
}

.area-name {
  font-size: $font-size-lg;
  font-weight: $font-weight-bold;
  color: $color-text-primary;
  margin-bottom: $spacing-xs;

  @include mobile {
    font-size: $font-size-base;
  }
}

.area-description {
  color: $color-text-secondary;
  font-size: $font-size-sm;
  line-height: 1.5;

  @include mobile {
    font-size: $font-size-xs;
  }
}

.btn-view-details {
  margin-top: $spacing-md;
  padding: $spacing-sm $spacing-lg;
  background: linear-gradient(135deg, rgba(33, 150, 243, 0.3), rgba(33, 150, 243, 0.5));
  border: 2px solid $color-info;
  border-radius: $border-radius-md;
  color: $color-info;
  font-size: $font-size-base;
  font-weight: $font-weight-bold;
  cursor: pointer;
  transition: all $transition-base;
  width: 100%;

  @include mobile {
    padding: $spacing-xs $spacing-md;
    font-size: $font-size-sm;
    border-width: 1px;
    margin-top: $spacing-sm;
  }

  &:hover {
    background: linear-gradient(135deg, rgba(33, 150, 243, 0.5), rgba(33, 150, 243, 0.7));
    box-shadow: 0 0 20px rgba(33, 150, 243, 0.3);
    transform: translateY(-2px);
  }

  &:active {
    transform: translateY(0);
  }
}
