
@import '../styles/global.scss';

.sanctuary-view {
  width: 100%;
  height: 100%;
  position: relative;
  overflow: hidden;
}

.sanctuary-container,
.chat-container {
  width: 100%;
  height: 100%;
  position: relative;
}

.sanctuary-background {
  width: 100%;
  height: 100%;
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  position: relative;
  background-color: #2a2a2a;
}

.furniture-item,
.memento-item {
  position: absolute;
  cursor: pointer;
  transition: transform $transition-fast;

  &:hover {
    transform: scale(1.05);
  }

  img {
    max-width: 100px;
    max-height: 100px;
    object-fit: contain;
  }
}

.placeholder-item,
.memento-placeholder {
  padding: $spacing-sm $spacing-md;
  background: rgba(0, 0, 0, 0.7);
  border: 2px solid $color-border-gold;
  border-radius: $border-radius-sm;
  color: $color-text-gold;
  font-size: $font-size-sm;
  white-space: nowrap;
}

.sanctuary-actions {
  position: absolute;
  bottom: $spacing-lg;
  left: 50%;
  transform: translateX(-50%);
  display: flex;
  gap: $spacing-md;
  z-index: 10;

  @include mobile {
    bottom: $spacing-md;
    gap: $spacing-sm;
    flex-wrap: wrap;
    justify-content: center;
  }
}

.action-btn {
  padding: $spacing-sm $spacing-lg;
  background: rgba(0, 0, 0, 0.8);
  border: 2px solid $color-border-gold;
  color: $color-text-gold;
  font-size: $font-size-base;
  cursor: pointer;
  transition: all $transition-fast;
  font-family: $font-family-primary;

  @include mobile {
    padding: $spacing-xs $spacing-md;
    font-size: $font-size-sm;
  }

  &:hover {
    background: rgba(212, 175, 55, 0.2);
    border-color: $color-primary-gold;
    transform: translateY(-2px);
    box-shadow: $shadow-gold;
  }

  &.close-btn {
    border-color: $color-border-dark;
    color: $color-text-secondary;

    &:hover {
      border-color: $color-text-secondary;
      background: rgba(255, 255, 255, 0.1);
    }
  }
}

.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.8);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 100;
}

.invite-dialog {
  @include modal-container;
  max-width: 600px;
  width: 90%;
  max-height: 80vh;
  overflow-y: auto;

  h3 {
    @include modal-title;
    margin-bottom: $spacing-lg;
  }
}

.journal-modal-container {
  width: 90%;
  max-width: 1200px;
  height: 85vh;
  max-height: 800px;
  border-radius: 12px;
  overflow: hidden;
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.6);

  @include mobile {
    width: 95%;
    height: 90vh;
  }
}

.npc-list {
  display: flex;
  flex-direction: column;
  gap: $spacing-sm;
  margin-bottom: $spacing-lg;
  max-height: 400px;
  overflow-y: auto;
}

.npc-option {
  label {
    display: flex;
    align-items: center;
    gap: $spacing-md;
    padding: $spacing-md;
    background: rgba(0, 0, 0, 0.4);
    border: 1px solid $color-border-dark;
    border-radius: $border-radius-sm;
    cursor: pointer;
    transition: all $transition-fast;

    &:hover {
      border-color: $color-border-gold;
      background: rgba(212, 175, 55, 0.1);
    }

    input[type='checkbox'] {
      width: 20px;
      height: 20px;
      cursor: pointer;
    }
  }
}

.npc-name {
  font-weight: $font-weight-bold;
  color: $color-text-primary;
  flex: 0 0 auto;
}

.npc-info {
  color: $color-text-secondary;
  font-size: $font-size-sm;
  flex: 1;
}

.dialog-actions {
  display: flex;
  gap: $spacing-md;
  justify-content: flex-end;

  @include mobile {
    flex-direction: column;
  }
}

.btn-primary,
.btn-secondary {
  padding: $spacing-sm $spacing-lg;
  font-size: $font-size-base;
  cursor: pointer;
  transition: all $transition-fast;
  font-family: $font-family-primary;

  @include mobile {
    padding: $spacing-sm $spacing-md;
    font-size: $font-size-sm;
  }
}

.btn-primary {
  background: $color-primary-gold;
  color: $color-primary-black;
  border: 2px solid $color-primary-gold;

  &:hover:not(:disabled) {
    background: lighten($color-primary-gold, 10%);
    transform: translateY(-2px);
    box-shadow: $shadow-gold;
  }

  &:disabled {
    opacity: 0.5;
    cursor: not-allowed;
  }
}

.btn-secondary {
  background: transparent;
  color: $color-text-secondary;
  border: 2px solid $color-border-dark;

  &:hover {
    border-color: $color-text-secondary;
    color: $color-text-primary;
  }
}

.placeholder-text {
  color: $color-text-secondary;
  text-align: center;
  padding: $spacing-xl;
  font-size: $font-size-base;
}

// ====================  ====================

.chat-container {
  display: flex;
  flex-direction: column;
  background: $color-bg-primary;
}

.chat-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: $spacing-md $spacing-lg;
  background: rgba(0, 0, 0, 0.8);
  border-bottom: 2px solid $color-border-gold;

  @include mobile {
    padding: $spacing-sm $spacing-md;
  }
}

.chat-title {
  font-size: $font-size-lg;
  font-weight: $font-weight-bold;
  color: $color-text-gold;

  @include mobile {
    font-size: $font-size-base;
  }
}

.back-btn {
  padding: $spacing-xs $spacing-md;
  background: transparent;
  border: 2px solid $color-border-dark;
  color: $color-text-secondary;
  font-size: $font-size-sm;
  cursor: pointer;
  transition: all $transition-fast;
  font-family: $font-family-primary;

  &:hover {
    border-color: $color-text-secondary;
    color: $color-text-primary;
    background: rgba(255, 255, 255, 0.1);
  }
}

.chat-messages {
  flex: 1;
  overflow-y: auto;
  padding: $spacing-lg;
  display: flex;
  flex-direction: column;
  gap: $spacing-md;

  @include mobile {
    padding: $spacing-md;
    gap: $spacing-sm;
  }
}

.no-messages {
  text-align: center;
  color: $color-text-secondary;
  padding: $spacing-xl;
  font-size: $font-size-base;
}

.message-item {
  display: flex;
  animation: fadeIn 0.3s ease-in-out;

  &.user {
    justify-content: flex-end;

    .message-content {
      background: rgba(212, 175, 55, 0.2);
      border-color: $color-border-gold;
    }
  }

  &.assistant {
    justify-content: flex-start;

    .message-content {
      background: rgba(0, 0, 0, 0.6);
      border-color: $color-border-light;
    }
  }
}

.message-content {
  max-width: 70%;
  padding: $spacing-md;
  border: 1px solid;
  border-radius: $border-radius-md;
  word-wrap: break-word;

  @include mobile {
    max-width: 85%;
    padding: $spacing-sm;
  }
}

.message-author {
  font-size: $font-size-sm;
  font-weight: $font-weight-bold;
  color: $color-text-gold;
  margin-bottom: $spacing-xs;
}

.message-text {
  font-size: $font-size-base;
  color: $color-text-primary;
  line-height: 1.6;
  white-space: pre-wrap;

  @include mobile {
    font-size: $font-size-sm;
  }
}

.chat-input-container {
  padding: $spacing-md $spacing-lg;
  background: rgba(0, 0, 0, 0.8);
  border-top: 2px solid $color-border-gold;
  display: flex;
  gap: $spacing-md;
  align-items: flex-end;

  @include mobile {
    padding: $spacing-sm $spacing-md;
    gap: $spacing-sm;
  }
}

.chat-input {
  flex: 1;
  min-height: 60px;
  max-height: 120px;
  padding: $spacing-sm;
  background: rgba(0, 0, 0, 0.5);
  border: 1px solid $color-border-light;
  border-radius: $border-radius-sm;
  color: $color-text-primary;
  font-size: $font-size-base;
  font-family: $font-family-primary;
  resize: vertical;
  outline: none;
  transition: border-color $transition-fast;

  @include mobile {
    min-height: 50px;
    max-height: 80px;
    font-size: 16px; //  iOS 
  }

  &:focus {
    border-color: $color-primary-gold;
  }

  &::placeholder {
    color: $color-text-muted;
  }
}

.send-btn {
  padding: $spacing-sm $spacing-lg;
  background: $color-primary-gold;
  color: $color-primary-black;
  border: 2px solid $color-primary-gold;
  font-size: $font-size-base;
  font-weight: $font-weight-bold;
  cursor: pointer;
  transition: all $transition-fast;
  white-space: nowrap;
  font-family: $font-family-primary;

  @include mobile {
    padding: $spacing-xs $spacing-md;
    font-size: $font-size-sm;
  }

  &:hover:not(:disabled) {
    background: lighten($color-primary-gold, 10%);
    transform: translateY(-2px);
    box-shadow: $shadow-gold;
  }

  &:disabled {
    opacity: 0.5;
    cursor: not-allowed;
  }
}

@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(10px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}
