
.test-panel {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 90%;
  max-width: 800px;
  max-height: 80vh;
  background: rgba(20, 20, 30, 0.95);
  border: 2px solid #d4af37;
  border-radius: 8px;
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.8);
  z-index: 1000;
  overflow: hidden;
  display: flex;
  flex-direction: column;
}

.test-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 16px 24px;
  background: linear-gradient(135deg, #d4af37, #aa8c2e);
  color: #1a1a2e;

  h2 {
    margin: 0;
    font-size: 20px;
    font-weight: bold;
  }

  .close-btn {
    background: none;
    border: none;
    color: #1a1a2e;
    font-size: 24px;
    cursor: pointer;
    padding: 0;
    width: 32px;
    height: 32px;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 4px;
    transition: background 0.2s;

    &:hover {
      background: rgba(0, 0, 0, 0.1);
    }
  }
}

.test-content {
  flex: 1;
  overflow-y: auto;
  padding: 24px;
}

.test-section {
  margin-bottom: 32px;
  padding: 16px;
  background: rgba(255, 255, 255, 0.05);
  border-radius: 8px;
  border: 1px solid rgba(212, 175, 55, 0.3);

  h3 {
    margin: 0 0 16px 0;
    color: #d4af37;
    font-size: 18px;
  }

  .test-actions {
    display: flex;
    gap: 12px;
    flex-wrap: wrap;
    margin-bottom: 16px;

    button {
      padding: 8px 16px;
      background: rgba(212, 175, 55, 0.2);
      border: 1px solid #d4af37;
      border-radius: 4px;
      color: #d4af37;
      cursor: pointer;
      transition: all 0.2s;
      font-size: 14px;

      &:hover {
        background: rgba(212, 175, 55, 0.3);
        transform: translateY(-2px);
      }

      &.primary-btn {
        background: linear-gradient(135deg, #d4af37, #aa8c2e);
        color: #1a1a2e;
        font-weight: bold;

        &:hover {
          background: linear-gradient(135deg, #e5c158, #d4af37);
        }
      }
    }
  }

  .test-result {
    padding: 12px;
    background: rgba(0, 0, 0, 0.3);
    border-radius: 4px;
    color: #e0e0e0;
    font-size: 13px;
    line-height: 1.6;
    white-space: pre-wrap;
    word-break: break-word;
    max-height: 300px;
    overflow-y: auto;

    pre {
      margin: 0;
      font-family: 'Courier New', monospace;
      font-size: 12px;
    }
  }
}
