
@import '../styles/global.scss';

.dialogue-box {
  position: fixed;
  bottom: var(--dialogue-bottom-offset);
  left: 50%;
  transform: translateX(-50%);
  width: calc(100% - var(--dialogue-side-margin));
  max-width: 800px;
  min-height: 120px;
  max-height: var(--dialogue-max-height);
  background: $color-bg-overlay;
  border: 2px solid $color-border-gold;
  border-radius: $border-radius-lg;
  padding: $spacing-lg;
  z-index: $z-index-dialogue;
  box-shadow: $shadow-lg, $shadow-gold;
  cursor: pointer;
  transition: all $transition-base;
  display: flex;
  flex-direction: column;
  box-sizing: border-box;

  @include mobile {
    min-height: 100px;
    padding: $spacing-md;
    border-width: 1px;
    border-radius: $border-radius-md;
  }

  @include small-screen {
    padding: $spacing-sm;
  }

  &:hover {
    border-color: $color-secondary-gold;
    box-shadow:
      $shadow-lg,
      0 0 30px rgba(212, 175, 55, 0.5);
  }
}

// 

.dialogue-display {
  width: 100%;
  flex: 1;
  display: flex;
  flex-direction: column;
  overflow-y: auto; // 
  min-height: 0; //  flex 
}

.dialogue-content {
  flex: 1;
  min-height: 0; //  flex 
}

.dialogue-text {
  font-size: $font-size-base;
  color: $color-text-primary;
  line-height: 1.8;
  white-space: pre-wrap;
  word-wrap: break-word;

  @include mobile {
    font-size: $font-size-sm;
    line-height: 1.6;
  }
}

.continue-hint {
  margin-top: $spacing-md;
  font-size: $font-size-sm;
  color: $color-text-muted;
  text-align: right;
  font-style: italic;
  animation: pulse 2s ease-in-out infinite;
}

@keyframes pulse {
  0%,
  100% {
    opacity: 0.5;
  }
  50% {
    opacity: 1;
  }
}

.dialogue-placeholder {
  font-size: $font-size-base;
  color: $color-text-secondary;
  text-align: center;
  padding: $spacing-xl 0;

  p {
    margin: $spacing-sm 0;
  }

  .hint {
    font-size: $font-size-sm;
    color: $color-text-muted;
    font-style: italic;
  }
}

.custom-input-container {
  margin-top: $spacing-md;
  padding-top: $spacing-md;
  border-top: 1px solid rgba(212, 175, 55, 0.3);
  display: flex;
  flex-direction: column;
  gap: $spacing-sm;
  width: 100%;
  box-sizing: border-box;
  flex-shrink: 0;

  @include mobile {
    margin-top: $spacing-sm;
    padding-top: $spacing-sm;
    gap: $spacing-xs;
  }

  @include small-screen {
    margin-top: $spacing-xs;
    padding-top: $spacing-xs;
    gap: 4px;
  }
}

.custom-input-field {
  width: 100%;
  min-height: 80px;
  max-height: 120px;
  padding: $spacing-md;
  background: rgba(0, 0, 0, 0.5);
  border: 1px solid $color-border-light;
  border-radius: $border-radius-md;
  color: $color-text-primary;
  font-size: $font-size-base;
  font-family: $font-family-primary;
  resize: vertical;
  outline: none;
  transition: border-color $transition-base;
  box-sizing: border-box;

  @include mobile {
    min-height: 50px;
    max-height: 80px;
    padding: $spacing-sm;
    font-size: 16px; //  iOS 
  }

  @include small-screen {
    min-height: 40px;
    max-height: 60px;
    padding: $spacing-xs;
  }

  &:focus {
    border-color: $color-primary-gold;
  }

  &::placeholder {
    color: $color-text-muted;
  }
}

.custom-send-button {
  align-self: flex-end;
  padding: $spacing-sm $spacing-lg;
  background: linear-gradient(135deg, $color-primary-gold, $color-dark-gold);
  border: none;
  border-radius: $border-radius-md;
  color: $color-primary-black;
  font-size: $font-size-base;
  font-weight: $font-weight-bold;
  cursor: pointer;
  transition: all $transition-base;
  box-shadow: $shadow-sm;
  white-space: nowrap;
  flex-shrink: 0;

  @include mobile {
    padding: $spacing-xs $spacing-md;
    font-size: $font-size-sm;
  }

  @include small-screen {
    padding: $spacing-xs $spacing-sm;
    font-size: $font-size-xs;
  }

  &:hover:not(:disabled) {
    background: linear-gradient(135deg, $color-secondary-gold, $color-primary-gold);
    box-shadow: $shadow-md, $shadow-gold;
    transform: translateY(-2px);
  }

  &:active:not(:disabled) {
    transform: translateY(0);
    box-shadow: $shadow-sm;
  }

  &:disabled {
    opacity: 0.5;
    cursor: not-allowed;
  }
}
